import React  from 'react'
import CSVReader from 'react-csv-reader'
import Column  from './Column.js'

export default class Parse extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data:[
          []
      ],
      options:{delimiter:";"},
      delimiters:[';',',','.','%']
    };
  }
  
  handleForce = data => {
    this.setState({data: data});
  }

  delimiterFunction = e => {
    this.setState({
        options:{delimiter: e.currentTarget.value}
      });
  }
  
  render() {
    return <div >
      <hr/>
        <h3>Paso 1: ingrese el delimitador</h3>
        <p>El delimitador puede ser ; , . % o una palabra clave</p>
        <div>Delimitador:   
            <div>
                <input type="text" name="delimiter" value={this.state.options.delimiter} onChange={this.delimiterFunction} ></input><br/>
            </div>
        </div>
        <hr/>
        <h3>Paso 2: Seleccione el arciho csv</h3>
        <div className="container">
            <CSVReader
            cssClass="react-csv-input"
            label="Select CSV with secret Death Star statistics"
            onFileLoaded={this.handleForce}
            parserOptions={this.state.options}
            />
        </div>
        <hr/>
        <Column data={this.state.data}/>
    </div>
  }

}