import 'isomorphic-fetch'
import { myConfig } from './config/config.js';

export default {
  genericCr,
}
async function genericCr(component) {
  const response = await fetch(myConfig.apiUrl + '/crmRegister/save',{method: 'POST',headers: {'Content-Type':'application/json'},body:JSON.stringify(component)});
  var data = await response.json();
  return data;
}
