import React  from 'react'
import api from '../api'

export default class Column extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
        fields:[
            { checked: false,value:'Nombres' },
            { checked: false,value:'Apellidos' },
            { checked: false,value:'Teléfonos' },
            { checked: false,value:'Direcciones' }
        ],
        check:[],
        size:0,
        error:false,
        textButton:'Enviar'
    };
  }

  componentWillMount = () => {
    this.selectedCheckboxes = new Set();
  }

  selectOption = e => {
    if (this.selectedCheckboxes.has(e.currentTarget.value)) {
        this.selectedCheckboxes.delete(e.currentTarget.value);
      } else {
        this.selectedCheckboxes.add(e.currentTarget.value);
      }
    this.setState({
        size:this.selectedCheckboxes.size
    });
  }

  parse() {
    this.setState({
      textButton:'Enviando....'
  });
      var data = this.props.data[0]
      var ids=[];
      this.selectedCheckboxes.forEach(box => {
          if(data.indexOf(box)!==-1){
            ids.push(data.indexOf(box))
          }
      });
      var total=[];
      var i = 0
      for(i in this.props.data){
        total.push({
            nombre:this.props.data[i][ids[0]],
            apellidos:this.props.data[i][ids[1]],
            telefonos:this.props.data[i][ids[2]],
            direcciones:this.props.data[i][ids[3]]
        })
      }
      this.sendApi(total)
  }

  async sendApi(total){
    try{
      const response =  await api.genericCr(total,'POST');
      alert(JSON.stringify(response))
      if(response && response.success===false){
        this.setState({
            error:true
        });
      }else{
        this.setState({
          error:false
      });
      }
    }catch(e){
      alert('error al enviar')
    }
    this.setState({
      textButton:'Enviar'
  });
  }

  show(d){
    var array = Array.from(this.selectedCheckboxes);
    var i = array.indexOf(d);     
    return [this.selectedCheckboxes.has(d),i]
  }

  
  render() {
    return <div >
        { this.props.data[0] && this.props.data[0].length>0 &&  
            <div>   
                <h3>Paso 3: seleccione los campos a subir </h3>
                <div className="container">
                    <p>las Columnas deben ser {this.state.fields.map((item,i) => <span key={i}>{item.value},</span>)} </p>
                    <div>
                        {this.props.data[0].map((d,index) => {
                        return (
                            <div key={index}>
                                <input type="checkbox" name="fields" value={d} onChange={this.selectOption}></input>{d} 
          
                                 <br/>
                            </div>
                        )
                        })}
                    </div>
                </div>
                <br/>
                {this.props.data[0].length<3 ||  this.selectedCheckboxes.size!==4 &&
                    <div className="alert alert-warning">
                        <strong>Advertencia!</strong> para completar el cargue de la informacion el archivo que suba debe contener los parametros de Nombres , Apellidos , Teléfonos , Direcciones.
                    </div>
                }
                {this.state.error &&
                    <div className="alert alert-danger">
                        <strong>Error!</strong> Error al cargar su información
                    </div>
                }
                
                <br/>
                <div>
                    <button className={"btn btn-primary" + (this.props.data[0].length>3 && this.selectedCheckboxes.size===4 ? '' : ' disabled hide')} onClick={this.parse.bind(this)}>{this.state.textButton}</button>
                </div>
                <br/>
                <br/>
            </div>
        }
    </div>
  }

}